import { TItemModel } from 'common';
import { ProjectItem } from 'components';
import React from 'react';
import { TResultsListProps } from './types';

export function ResultsListMobile({ items }: TResultsListProps): JSX.Element {
  return (
    <div>
      {items.map((project: TItemModel) => (
        <ProjectItem
          key={`${project.project}-${project.photo.url}`}
          id={project.project}
          projectName={project.projectName}
          imageUrl={project.photo.url}
        />
      ))}
    </div>
  );
}
