import {
  introService,
  routerHistory,
  routerPaths,
  useRx
} from 'common';
import { Header } from 'components';
import {
  AllProjectsPage,
  ContactUsPage,
  MainPage,
  ProjectPage,
  ResultsPage,
  StudioPage
} from 'pages';
import React from 'react';
import {
  Router,
  Route,
  Switch
} from 'react-router';


export function SyndicatePage(): JSX.Element {
  const isShowHeader = useRx(introService.introViewed$)
  || routerHistory.location.pathname !== routerPaths.MAIN;

  return (
    <>
      {isShowHeader && (
        <Header />
      )}
      <Router history={routerHistory}>
        <Switch>
          <Route path={routerPaths.MAIN} exact component={MainPage} />
          <Route path={routerPaths.STUDIO} exact component={StudioPage} />
          <Route path={routerPaths.PROJECTS} exact component={AllProjectsPage} />
          <Route path={routerPaths.USER_CHOOSE_RESULTS} exact component={ResultsPage} />
          <Route path={routerPaths.PROJECT} component={ProjectPage} />
          <Route path={routerPaths.CONTACT_US} component={ContactUsPage} />
        </Switch>
      </Router>
    </>
  );
}
