import { useMobile } from 'common';
import React from 'react';
import { ResultsListDesktop } from './results-list-desktop';
import { ResultsListMobile } from './results-list-mobile';
import { TResultsListProps } from './types';


export function ResultsList({ items }: TResultsListProps): JSX.Element {
  const isMobile = useMobile();

  if (isMobile) {
    return <ResultsListMobile items={items} />;
  }

  return <ResultsListDesktop items={items} />;
}
