import { TItemModel } from 'common';
import { ProjectItem } from 'components';
import React from 'react';
import { TResultsListProps } from './types';
import { leftColumnStyle, resultsPageStyle, rightColumnStyle } from '../results-page.style';

export function ResultsListDesktop({ items }: TResultsListProps): JSX.Element {
  const left: Array<JSX.Element> = [];
  const right: Array<JSX.Element> = [];

  items.forEach((project: TItemModel, index: number) => {
    const element = (
      <ProjectItem
        key={`${project.project}-${project.photo.url}`}
        id={project.project}
        projectName={project.projectName}
        imageUrl={project.photo.url}
      />
    );
    if (index % 2 === 0) {
      left.push(element);
    } else {
      right.push(element);
    }
  });

  if (left.length <= 2) {
    left.push(<div className={leftColumnStyle} />);
  }

  if (right.length <= 2) {
    right.push(<div className={rightColumnStyle} />);
  }

  return (
    <div className={resultsPageStyle}>
      <div>{left}</div>
      <div>{right}</div>
    </div>
  );
}
