import {
  Navigator,
  TItemModel,
  TItems,
  UserSwipe
} from 'common';
import {
  ButtonsContainer,
  Detailled,
  Layout,
  RoundedButton,
  Text
} from 'components';
import React from 'react';
import { AllItemsSwipedPage } from './all-items-swiped-page';
import {
  choosePageStyle,
  chooseStateScreenStyle,
  detailedWrapperInfo
} from './choose-page.style';

type TChoosePageContentProps = {
  itemsQueue: TItems;
  onSelectItem: (item: TItemModel, decision: UserSwipe) => void;
};

enum ChooseState {
  LIKE = 'like',
  DISLIKE = 'dislike'
}

const timeoutMS = 200;

export function ChoosePageContent({ itemsQueue, onSelectItem }: TChoosePageContentProps): JSX.Element {
  const [chooseState, setChooseState] = React.useState<'' | ChooseState>('');

  const firstItemInQueue = itemsQueue[0];

  const handleSelectItem = React.useCallback(() => {
    setChooseState(ChooseState.LIKE);
    setTimeout(() => {
      setChooseState('');
      onSelectItem(firstItemInQueue, UserSwipe.SELECT);
    }, timeoutMS);
  }, [firstItemInQueue, onSelectItem]);

  const handleRejectItem = React.useCallback(() => {
    setChooseState(ChooseState.DISLIKE);
    setTimeout(() => {
      setChooseState('');
      onSelectItem(firstItemInQueue, UserSwipe.REJECT);
    }, timeoutMS);
  }, [firstItemInQueue, onSelectItem]);

  const handleOpenDetailedInfo = React.useCallback(() =>
    Navigator.navigateToProject(firstItemInQueue.project), [firstItemInQueue]);

  if (!firstItemInQueue) {
    return (
      <AllItemsSwipedPage />
    );
  }

  return (
    <Layout noFooter>
      <div className={choosePageStyle(firstItemInQueue.photo.url)}>
        <div className={detailedWrapperInfo}>
          <Detailled onOpenDetailedInfo={handleOpenDetailedInfo} projectName={firstItemInQueue.projectName} />
        </div>
      </div>
      {chooseState && (
        <div className={chooseStateScreenStyle}>
          <Text size="l" color="white">{chooseState}</Text>
        </div>
      )}
      <ButtonsContainer>
        <RoundedButton type="cross" onClick={handleRejectItem} />
        <RoundedButton type="heart" onClick={handleSelectItem} />
      </ButtonsContainer>
    </Layout>
  );
}
